import PopupWithForm from "./PopupWithForm";
import React from "react";

function EditAvatarPopup(props) {
  const userAvatar = React.useRef(); // –∑–∞–ø–∏—Å—ã–≤–∞–µ–º –æ–±—ä–µ–∫—Ç, –≤–æ–∑–≤—Ä–∞—â–∞–µ–º—ã–π —Ö—É–∫–æ–º, –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é
  function handleSubmit(e) {
    // –ó–∞–ø—Ä–µ—â–∞–µ–º –±—Ä–∞—É–∑–µ—Ä—É –ø–µ—Ä–µ—Ö–æ–¥–∏—Ç—å –ø–æ –∞–¥—Ä–µ—Å—É —Ñ–æ—Ä–º—ã
    e.preventDefault();
    // –ü–µ—Ä–µ–¥–∞—ë–º –∑–Ω–∞—á–µ–Ω–∏—è —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤–æ –≤–Ω–µ—à–Ω–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫
    props.onUpdateAvatar(userAvatar.current.value);
  }
  return (
    <PopupWithForm
      className="overlay_type_setAvatar"
      popupTitle="–û–±–Ω–æ–≤–∏—Ç—å –∞–≤–∞—Ç–∞—Ä"
      isOpen={props.isOpen}
      onClose={props.onClose}
      formName="popup_avatar"
      formButtonContent="–û–±–Ω–æ–≤–∏—Ç—å"
      onSubmit={handleSubmit}
    >
      <label className="form__label">
        <input
          type="url"
          ref={userAvatar}
          name="newAvatar"
          className="form__input form__input_job form__input-active"
          required
          placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –Ω–æ–≤—ã–π –∞–≤–∞—Ç–∞—Ä"
          defaultValue=""
        />
        <span className="form-link-avatar-error"></span>
      </label>
    </PopupWithForm>
  );
}
export default EditAvatarPopup;

