import PopupWithForm from "./PopupWithForm";
import CurrentUserContext from "../contexts/CurrentUserContext";
import React from "react";
import { useState } from "react";
function EditProfilePopup(props) {
  const [name, setName] = useState("");
  const [description, setAbout] = useState("");

  const currentUser = React.useContext(CurrentUserContext);
  React.useEffect(() => {
    if (currentUser) {
      setName(currentUser.name);
      setAbout(currentUser.about);
    }
  }, [currentUser]);

  function handleChangeName(e) {
    setName(e.target.value);
  }
  function handleChangeAbout(e) {
    setAbout(e.target.value);
  }
  function handleSubmit(e) {
    // –ó–∞–ø—Ä–µ—â–∞–µ–º –±—Ä–∞—É–∑–µ—Ä—É –ø–µ—Ä–µ—Ö–æ–¥–∏—Ç—å –ø–æ –∞–¥—Ä–µ—Å—É —Ñ–æ—Ä–º—ã
    e.preventDefault();
    console.log(e);
    // –ü–µ—Ä–µ–¥–∞—ë–º –∑–Ω–∞—á–µ–Ω–∏—è —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤–æ –≤–Ω–µ—à–Ω–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫

    props.onUpdateUser({
      name: name ,
      about: description,
    });
  }
  return (
    <PopupWithForm
      className="overlay_type_edit"
      popupTitle="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø—Ä–æ—Ñ–∏–ª—å"
      isOpen={props.isOpen}
      onClose={props.onClose}
      formName="profile-popup"
      formButtonContent="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"
      onSubmit={handleSubmit}
    >
      <label className="form__label">
        <input
          type="text"
          minLength="2"
          maxLength="40"
          name="name"
          className="form__input form__input_name"
          required
          placeholder="–í–∞—à–µ –∏–º—è"
          value={name}
          onChange={handleChangeName}
        />
      </label>
      <span className="form-name-input-error"></span>
      <label className="form__label" />
      <input
        type="text"
        minLength="2"
        maxLength="200"
        name="job"
        className="form__input form__input_job"
        required
        placeholder="–í–∞—à–∞ –ø—Ä–æ—Ñ–µ—Å—Å–∏—è"
        value={description}
        onChange={handleChangeAbout}
      />
      <span className="form-job-input-error"></span>
    </PopupWithForm>
  );
}
export default EditProfilePopup;