class Api {
  constructor({ link, headers }) {
    this._baseUrl = link;
    this._headers = headers;
  }
    

  _checkResponse(res) {
    if (res.ok) {
      return res.json();
    } else {
      return Promise.reject(new Error("–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞ ${res.status}"));
    }
  }
  // –ü–æ–ª—É—á–µ–Ω–∏–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–π –ø—Ä–æ—Ñ–∏–ª—è —Å —Å–µ—Ä–≤–µ—Ä–∞
  getUserInform() {
    return fetch(`${this._baseUrl}/users/me`, {
        method: 'GET',
      // credentials: 'include',
      headers: this._headers,
    }).then((res) => {
      return this._checkResponse(res);
    });
  }
  // –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–π –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ
  setUserInform(name, profession) {
    return fetch(`${this._baseUrl}/users/me`, {
      method: "PATCH",
     // credentials: 'include',
       headers: this._headers,
      body: JSON.stringify({
        name: name,
        about: profession,
      }),
    }).then((res) => {
      return this._checkResponse(res);
    });
  }
  // –û–±–Ω–æ–≤–ª–µ–Ω–∏–µ –∞–≤–∞—Ç–∞—Ä–∞ –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ
  getAvatarProfile(avatar) {
    return fetch(`${this._baseUrl}/users/me/avatar`, {
      method: "PATCH",
     // credentials: 'include',
       headers: this._headers,
      body: JSON.stringify({
       avatar: avatar
      }),
    }).then((res) => {
      return this._checkResponse(res);
    });
  }
  // –ü–æ–ª—É—á–µ–Ω–∏–µ –æ–±–µ–∫—Ç–∞ –∫–∞—Ä—Ç–æ—á–µ–∫ —Å —Å–µ—Ä–≤–µ—Ä–∞
  getIntialCards() {
    return fetch(`${this._baseUrl}/cards`, {
      method: 'GET',
     // credentials: 'include',
       headers: this._headers,
    }).then((res) => {
      return this._checkResponse(res);
    });
  }
  // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–µ–∫ –Ω–∞ —Å–µ—Ä–≤–µ—Ä
  photoAddServer(newCard) {
    return fetch(`${this._baseUrl}/cards`, {
      method: "POST",
     // credentials: 'include',
       headers: this._headers,
      body: JSON.stringify({
        name: newCard.name,
        link: newCard.link,
      }),
    }).then((res) => {
      return this._checkResponse(res);
    });
  }
  // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ/—É–¥–∞–ª–µ–Ω–∏–µ –ª–∞–π–∫–æ–≤
 LikeCard(id, like) {
        if (like) {
            return fetch(`${this._baseUrl}/cards/${id}/likes`, {
                method: 'PUT',
               // credentials: 'include',
                 headers: this._headers,
            })
            .then(res => {return this._checkResponse(res)})
         } else {
            return fetch(`${this._baseUrl}/cards/${id}/likes`, {
                method: 'DELETE',
              //  credentials: 'include',
                 headers: this._headers,
            })
            .then(res => {return this._checkResponse(res)})
         }
    }
  // —É–¥–∞–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–µ–∫
  deleteCard(id) {
    return fetch(`${this._baseUrl}/cards/${id}`, {
      method: "DELETE",
    //  credentials: 'include',
       headers: this._headers,
    }).then((res) => {
      return this._checkResponse(res);
    });
  }

    updateHeaders() {
      this._headers = {
        'Content-Type': 'application/json',
        'Authorization':  `Bearer ${localStorage.getItem('jwt')}`
      }
    }

}
const api = new Api({
  link: "https://pictures-host.nomoredomains.rocks",
   headers: {
   'Authorization': `Bearer ${localStorage.getItem('jwt')}`,
    "Content-Type": "application/json",
  },
});
export default api;
