import React from "react";
import CurrentUserContext from "../contexts/CurrentUserContext";

function Card(props) {
  const currentUser = React.useContext(CurrentUserContext);
  // –û–ø—Ä–µ–¥–µ–ª—è–µ–º, —è–≤–ª—è–µ–º—Å—è –ª–∏ –º—ã –≤–ª–∞–¥–µ–ª—å—Ü–µ–º —Ç–µ–∫—É—â–µ–π –∫–∞—Ä—Ç–æ—á–∫–∏
  const isOwn = props.card.owner === currentUser._id;
  // –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –∫–æ—Ç–æ—Ä—É—é –ø–æ—Å–ª–µ –∑–∞–¥–∞–¥–∏–º –≤ `className` –¥–ª—è –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è
  const cardDeleteButtonClassName = `popup_element ${
    isOwn ? "elemenet__icon-delete" : ""
  }`;
  // –û–ø—Ä–µ–¥–µ–ª—è–µ–º, –µ—Å—Ç—å –ª–∏ —É –∫–∞—Ä—Ç–æ—á–∫–∏ –ª–∞–π–∫, –ø–æ—Å—Ç–∞–≤–ª–µ–Ω–Ω—ã–π —Ç–µ–∫—É—â–∏–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º
  const isLiked = props.card.likes.some((i) => i === currentUser._id);

  // –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –∫–æ—Ç–æ—Ä—É—é –ø–æ—Å–ª–µ –∑–∞–¥–∞–¥–∏–º –≤ `className` –¥–ª—è –∫–Ω–æ–ø–∫–∏ –ª–∞–π–∫–∞
  const cardLikeButtonClassName = `element__vector-like ${
    isLiked ? "element__vector-active" : ""
  }`;
  function handleLikeClick() {
    props.onCardLike(props.card);
  }
  function handleDeleteClick() {
    props.onCardDelete(props.card);
  }
  function handleClick() {
    props.onCardClick(props.card);
  }
  return (
    <div className="element">
      <button
        className={cardDeleteButtonClassName}
        onClick={handleDeleteClick}
      ></button>
      <img
        className="element__image"
        alt={props.card.name}
        src={props.card.link}
        onClick={handleClick}
      />
      <h2 className="element__title">{props.card.name}</h2>
      <div className="element__likes">
        <button
          className={cardLikeButtonClassName}
          type="button"
          onClick={handleLikeClick}
        ></button>
        <p className="element__like">{props.card.likes.length}</p>
      </div>
    </div>
  );
}
export default Card;

